.cart_card_container {
  width: 800px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px;
}

.cart_card_container img {
  width: 33px;
  height: 50px;
  background-color: rgb(187, 237, 237);
}

.pluss_minus {
